* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
body {
  background-color: antiquewhite;
  font-family: Arial, Helvetica, sans-serif;
}
.person {
  background-color: lightgrey;
  width: 17rem;
  padding: 1rem;
  margin: 2rem auto;
  border-radius: 0.6rem;
  text-align: center;
  cursor: pointer;
  transition: all 0.3s linear;
}
.person-list {
  display: flex;
  flex-direction: row;
}
.person img {
  border-radius: 50%;
  border-radius: 0.1rem solid black;
  width: 90px;
  height: 90px;
}
.person h4 {
  text-transform: uppercase;
  margin: 0 0;
  letter-spacing: 0.15rem;
  color: blueviolet;
  cursor: pointer;
}
.person:hover {
  box-shadow: 2px 5px 10px red;
}

/* mobile view */
@media screen and (max-width: 768px) {
  .person-list {
    display: block;
  }
}
